import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { AlertController, NavController } from '@ionic/angular';
import { createAnimation } from '@ionic/core';
import { Observable } from 'rxjs';
import { Equipment } from 'src/model/equipment';
import { EquipmentReservation, Reservation } from 'src/model/room';
import { EquipmentServiceService } from '../equipment-service.service';
import { Store, select } from '@ngrx/store';
import { FormBuilder, FormGroup, } from '@angular/forms';
import { Notification } from 'src/app/store/models/notification';
import { TodoAdd } from '../../../store//actions/notification-action';
import { GeneralFunctions } from '../../general-functions';


@Component({
  selector: 'app-equipment-reservation',
  templateUrl: './equipment-reservation.page.html',
  styleUrls: ['./equipment-reservation.page.scss'],
})
export class EquipmentReservationPage implements OnInit {

  inputStart: boolean;
  inputEnd: boolean;
  equipments: any[] = [];
  isBottomSheetOpened = false;
  localstorageReservations: EquipmentReservation[] = [];
  registerForm: any;
  bookingForm: FormGroup;
  reservation;
  currentEquipment!: Equipment | null;
  equipmentName: string;
  equipmentHistorical = [];
  multiplicateReservation: boolean;
  wrongReservation: boolean;
  convertedDate;
  localISOTime;
  currentDate;
  showMap: boolean = false;
  showReservationsContent: boolean = true;
  notification: Observable<any[]>;

  public pageInfo = {title:'Réservation d\'', subTitle: 'équipements'};
  constructor(
    private equipmentsService: EquipmentServiceService,
    private fb: FormBuilder,
    public router: Router,
    public generalFunctions: GeneralFunctions

  ) { }

  initializeForm(): void {
    this.bookingForm = this.fb.group({
      dateStart: '',
      dateEnd: '',
      references: this.fb.array([this.fb.control('')])
    });
  }

  ngAfterViewInit(){
    this.equipments = this.equipmentsService.getAllEquipments();
    this.generalFunctions.gestionDeReservation(this.equipments, 'reservationsEquipment', 'historicalEquipmentsReservations');

  }

  ngOnInit() {
    this.initializeForm();
    setInterval(() => {
      this.generalFunctions.refreshComponent(this.ngAfterViewInit());
    }, 1000);
  }

}
